<template>
  <div>
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>专业管理</el-breadcrumb-item>
      <el-breadcrumb-item>课程列表</el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <!-- 搜索 -->
      <el-row :gutter="20">
        <el-col :span="24">
          <el-form ref="formRef" :model="queryVo" label-width="80px" inline>
            <el-form-item
              label="课程名称"
              prop="cname"
              label-width="100px"
            >
              <el-input
                v-model="queryVo.cname"
                @keyup.enter.native="queryPage"
                placeholder="请输入课程名称"
                clearable
                @clear="queryPage"
              ></el-input>
            </el-form-item>

            <el-form-item
              label="人才培养方案"
              prop="tpName"
              label-width="100px"
            >
              <el-input
                v-model="queryVo.tpName"
                @keyup.enter.native="queryPage"
                placeholder="请输入人才培养方案"
                clearable
                @clear="queryPage"
              ></el-input>
            </el-form-item>
            <el-form-item label="课程性质" prop="cnatureName">
              <el-input
                v-model="queryVo.cnatureName"
                @keyup.enter.native="queryPage"
                placeholder="请输入课程性质"
                clearable
                @clear="queryPage"
              ></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" icon="el-icon-search" @click="queryPage"
                >搜索</el-button
              >
            </el-form-item>
          </el-form>
        </el-col>
      </el-row>
      <el-row :gutter="10" class="el-row">
        <el-col :span="1.5">
          <el-button
            type="primary"
            icon="el-icon-plus"
              v-if="is_authority.is_user_add"
            size="mini"
            @click="open = true"
            >新增</el-button
          >
          
        </el-col>
        
        <el-col :span="1.5">
          <el-button
            type="success"
            icon="el-icon-edit"
            size="mini"
            :disabled="single"
            @click="editUser"
            v-if="is_authority.is_user_upt"
            >修改</el-button
          >
          
        </el-col>
        <el-col :span="1.5">
          <el-button
            type="danger"
            icon="el-icon-delete"
            size="mini"
            :disabled="multiple"
            v-if="is_authority.is_user_del"
            @click="del"
            >删除</el-button
          >
          
        </el-col>
        <el-col :span="1.5">
          <el-button type="info" icon="el-icon-upload2" size="mini"
            >导入</el-button
          >
          <!-- @click="handleImport" -->
          <!-- v-hasPermi="['system:user:import']" -->
        </el-col>
        <el-col :span="1.5">
          <el-button type="warning" icon="el-icon-download" size="mini"
            >导出</el-button
          >
          <!-- @click="handleExport"
                      v-hasPermi="['system:user:export']" -->
        </el-col>
        <!-- <right-toolbar :showSearch.sync="showSearch" @queryTable="getList"></right-toolbar> -->
      </el-row>
      <!-- 数据 -->
      <el-table
        :data="List"
        border
        stripe
        @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="50" align="center" />

        <el-table-column
          align="center"
          prop="cnumber"
          label="课程代码"
        ></el-table-column>
        <el-table-column
          align="center"
          prop="cname"
          label="课程名称"
        ></el-table-column>
        <el-table-column
          align="center"
          prop="ctype"
          label="课程类型"
        ></el-table-column>
        <el-table-column
          align="center"
          prop="copeningUnit"
          label="开课单位"
        ></el-table-column>
        <el-table-column
          align="center"
          prop="ccredit"
          label="学分"
        ></el-table-column>
        <el-table-column
          align="center"
          prop="ctotalHours"
          label="总学时"
        ></el-table-column>
        <el-table-column
          align="center"
          prop="ctheoreticalHours"
          label="理论学时"
        ></el-table-column>
        <el-table-column
          align="center"
          prop="cexperimentalHours"
          label="实验学时"
        ></el-table-column>
        <el-table-column
          align="center"
          prop="cpracticeHours"
          label="实践学时"
        ></el-table-column>
        <el-table-column
          align="center"
          prop="cstudyTerm"
          label="修读学期"
        ></el-table-column>
        <el-table-column
          align="center"
          prop="cnatureName"
          label="课程性质"
        ></el-table-column>
        <el-table-column
          align="center"
          prop="tpName"
          label="人才培养方案"
        ></el-table-column>
        <el-table-column
          align="center"
          prop="cdemo"
          label="简介"     
          show-overflow-tooltip
        ></el-table-column>

        <el-table-column label="操作" width="120px" align="center">
          <template slot-scope="scope">
            <!-- 修改 -->
            <el-tooltip
              class="item"
              effect="dark"
              content="编辑"
              v-if="is_authority.is_user_upt"
              placement="top-start"
              :enterable="false"
            >
              
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="editUser(scope.row)"
              ></el-button>
            </el-tooltip>
            <!-- 删除 -->
            <el-tooltip
              class="item"
              effect="dark"
              content="删除"  v-if="is_authority.is_user_del"
              placement="top-start"
              :enterable="false"
            >
              
              <el-button
                type="danger"
                icon="el-icon-delete"
                size="mini"
                @click="del(scope.row)"
              ></el-button>
            </el-tooltip>

            
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="page.current"
        :page-sizes="[5, 10, 20, 30, 40]"
        :page-size="page.size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="page.total"
      ></el-pagination>
      <!-- 对话框 :before-close="handleClose" 在对话框关闭之前执行-->
      <!-- 标题 -->
      <el-dialog
        title="编辑"
        :visible.sync="open"
        width="50%"
        @close="closeInit"
      >
        <!-- 内容主体 -->

        <el-form
          :model="Form"
          :rules="rules"
          ref="formRef"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="课程代码" prop="cnumber">
                <el-input
                  v-model="Form.cnumber"
                  placeholder="请输入内容"
                ></el-input>
              </el-form-item>
            </el-col>

            <el-col :span="12">
              <el-form-item label="课程名称" prop="cname">
                <el-input
                  v-model="Form.cname"
                  placeholder="请输入内容"
                ></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-form-item label="课程类型" prop="ctype">
            <el-input
              v-model="Form.ctype"
              placeholder="请输入内容"
            ></el-input>
          </el-form-item>
          <el-form-item label="开课单位" prop="copeningUnit">
            <el-input
              v-model="Form.copeningUnit"
              placeholder="请输入内容"
            ></el-input>
          </el-form-item>
          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="学分" prop="ccredit">
                <el-input
                  v-model="Form.ccredit"
                  placeholder="请输入内容"
                ></el-input>
              </el-form-item>
            </el-col>

            <el-col :span="12">
              <el-form-item label="修读学期" prop="cstudyTerm">
                <el-input
                  v-model="Form.cstudyTerm"
                  placeholder="请输入内容"
                ></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="总学时" prop="ctotalHours">
                <el-input
                  v-model="Form.ctotalHours"
                  placeholder="请输入内容"
                ></el-input>
              </el-form-item>
            </el-col>

            <el-col :span="12">
              <el-form-item label="理论学时" prop="ctheoreticalHours">
                <el-input
                  v-model="Form.ctheoreticalHours"
                  placeholder="请输入内容"
                ></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="实验学时" prop="cexperimentalHours">
                <el-input
                  v-model="Form.cexperimentalHours"
                  placeholder="请输入内容"
                ></el-input>
              </el-form-item>
            </el-col>

            <el-col :span="12">
              <el-form-item label="实践学时" prop="cpracticeHours">
                <el-input
                  v-model="Form.cpracticeHours"
                  placeholder="请输入内容"
                ></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="课程性质" prop="cnatureId">
                <el-select
                  v-model="Form.cnatureId"
                  filterable
                  placeholder="请输入内容"
                >
                  <el-option
                    v-for="item in couNOptions"
                    :key="item.id"
                    :label="item.cnatureName"
                    :value="item.id"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>

            <el-col :span="12">
              <el-form-item label="人才培养方案" prop="tpId" label-width="110px">
                <el-select
                  v-model="Form.tpId"
                  filterable
                  placeholder="请输入内容"
                >
                  <el-option
                    v-for="item in tpOptions"
                    :key="item.id"
                    :label="item.tpName"
                    :value="item.id"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
          <el-form-item label="简介"     show-overflow-tooltip prop="cdemo">
            <el-input
              v-model="Form.cdemo"
              placeholder="请输入内容"
            ></el-input>
          </el-form-item>
        </el-form>
        <!-- 按钮 -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="open = false">取 消</el-button>
          <el-button type="primary" @click="submitForm">确 定</el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>
<script>
import { mapState } from 'vuex'
export default {
  data() {
    return {
      is_authority: {
        is_user_del: false,
        is_user_upt: false,
        is_user_add: false,
        is_user_add_role: false
      },
      open: false,
      couNOptions: [],
      tpOptions: [],
      Form: {},
      rules: {
        cnumber: [
          { required: true, message: '不能为空', trigger: 'blur' }
        ],
        cname: [
          { required: true, message: '不能为空', trigger: 'blur' }
        ],
        ctype: [
          { required: true, message: '不能为空', trigger: 'blur' }
        ],
        copeningUnit: [
          { required: true, message: '不能为空', trigger: 'blur' }
        ],
        ccredit: [
          { required: true, message: '不能为空', trigger: 'blur' }
        ],
        cstudyTerm: [
          { required: true, message: '不能为空', trigger: 'blur' }
        ],
        ctotalHours: [
          { required: true, message: '不能为空', trigger: 'blur' }
        ],
        ctheoreticalHours: [
          { required: true, message: '不能为空', trigger: 'blur' }
        ],
        cexperimentalHours: [
          { required: true, message: '不能为空', trigger: 'blur' }
        ],
        cpracticeHours: [
          { required: true, message: '不能为空', trigger: 'blur' }
        ],
        cnatureId: [
          { required: true, message: '不能为空', trigger: 'blur' }
        ],
        tpId: [
          { required: true, message: '不能为空', trigger: 'blur' }
        ],
        cdemo: [
          { required: true, message: '不能为空', trigger: 'blur' }
        ]
      },
      // 非单个禁用
      single: true,
      // 非多个禁用
      multiple: true,
      List: [],
      queryVo: {
        tpName: undefined,
        page: {
          current: 1,
          size: 5
        }
      },
      page: {
        current: 1,
        size: 5,
        total: 0
      },
      ids: []
    }
  },
  created() {
    this.isAuthority()
    this.queryPage()
    this.getTreeselect()
  },
  computed: {
    ...mapState(['btus']),
  },
  methods: {
    isAuthority() {
      var arr = this.btus.split(',')
      for (var key in this.is_authority) {
        if (arr.indexOf(key) != -1) {
          this.is_authority[key] = true
        }
      }
    },
    async getTreeselect() {
      const { data: res } = await this.$http.post(
        '/trainingPlanInfo/queryTrainingPlanInfoList',
        {
          page: {
            current: 1,
            size: 999
          }
        }
      )
      const { data: res1 } = await this.$http.post(
        '/courseNatureInfo/queryCourseNatureInfoList',
        {
          page: {
            current: 1,
            size: 999
          }
        }
      )
      console.log(res.data.dataList.records)
      console.log(res1.data.dataList.records)
      const a = res.data.dataList.records
      this.tpOptions = this.unique(a)
      this.couNOptions = res1.data.dataList.records
      if (res.code != 20000) return this.$message.error(res.message)
    },

    unique(arr) {
      const res = new Map()
      return arr.filter(
        arr => !res.has(arr.tpName) && res.set(arr.tpName, 1)
      )
    },
    submitForm() {
      this.$refs.formRef.validate(async valid => {
        if (valid) {
          const { data: res } = await this.$http.post(
            '/couresInfo/saveOrUpdateCouresInfo',
            this.Form
          )
          if (res.code != 20000) return this.$message.error(res.message)
          this.open = false
          this.queryPage()
          return this.$message.success(res.message)
        }
      })
    },

    closeInit() {
      // console.log(this.Form);
      this.Form = {}
      this.$refs.formRef.resetFields()
    },

    async editUser(row) {
      this.open = true
      const id = row.id || this.ids
      console.log(id)
      const { data: res } = await this.$http.get(
        '/couresInfo/getByIdCouresInfo/' + id
      )
      if (res.code != 20000) return this.$message.error(res.message)
      console.log(res)
      this.Form = res.data.entity
      this.Form.cnatureId = this.Form.cnatureId+''
      this.Form.tpId = this.Form.tpId+''
    },
    del(row) {
      if (row.id) {
        var a = row.id.split(',')
      }
      const userid = a || this.ids
      console.log(userid)
      this.$confirm('确认要删除吗?', '警告', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          await this.$http.delete('/couresInfo/removeByIdCouresInfos', {
            data: userid
          })
          // console.log(res);
          this.queryPage()
          this.List = []
        })
        .then(() => {
          this.$message({
            showClose: true,
            message: '删除成功',
            type: 'success'
          })
        })
    },
    async queryPage() {
      const { data: res } = await this.$http.post(
        'couresInfo/queryCouresInfoList',
        this.queryVo
      )
      console.log(res.data.dataList.records)
      if (res.code != 20000) return this.$message.error(res.message)
      this.List = res.data.dataList.records
      this.page.total = res.data.dataList.total
      this.page.current = res.data.dataList.current
      this.page.size = res.data.dataList.size
      // console.log(this.List)
    },
    //选择框
    handleSelectionChange(selection) {
      this.ids = selection.map(item => item.id)
      this.single = selection.length != 1
      this.multiple = !selection.length
    },
    //更新状态
    async uptStatus(status) {
      const { data: res } = await this.$http.put(
        `/jxyxUser/users/${status.id}/state/${status.mgState}`
      )
      if (res.code != 20000) {
        status.mgState = !status.mgState
        return this.$message.error(res.message)
      }
      return this.$message.success(res.message)
    },
    // 监听每页展示的条数
    handleSizeChange(newSize) {
      this.queryVo.page.size = newSize
      this.queryPage()
    },
    //监听页码
    handleCurrentChange(newCurrent) {
      this.queryVo.page.current = newCurrent
      this.queryPage()
    }
  }
}
</script>
<style lang="less" scoped>
.el-table {
  margin-top: 15px;
  font-size: 12px;
}
.el-row {
  display: flex;
  align-items: center;
}
.el-pagination {
  margin-top: 15px;
}
</style>
